import Card from "react-bootstrap/Card";
import Table from "react-bootstrap/Table";

// Context
import CalculationContext from "../CalculationPageContext/calculation-context";

import {useContext, useEffect, useState} from "react";
import axios from "axios";

//CSS
import "./CalculationSecurityExceptions.css"

const CalculationSecurityExceptions = (props) => {
    const selectedEntity = useContext(CalculationContext)['selectedEntity'][1];
    const selectedDate = useContext(CalculationContext)['currentDate'];
    const [exceptions, setExceptions] = useState([]);
    const securityExceptions = exceptions.map((data) =>
        <tr onDoubleClick={() => alert(data['id'])} key={data['id']} style={{color: 'red'}}>
            <td className="table-row">{data['exception_level']}</td>
            <td className="table-row">{data['entity_code']}</td>
            <td className="table-row">{data['exception_type']}</td>
            <td className="table-row">{data['process']}</td>
            <td className="table-row">{data['security_id']}</td>
            <td className="table-row">{data['calculation_date']}</td>
            <td className="table-row">{data['status']}</td>
        </tr>);
    useEffect(() => {
            axios.get(props.server + 'exceptions/get/', {
                params:{
                    entity_code: selectedEntity,
                    exception_level: 'Security',
                    calculation_date: props.calcDate,
                }
            })
                .then(response => setExceptions(response['data']))
                .catch((error) => {
                    console.error('Error Message:', error);
                });
        }, [props]
    );
    return(
        <Card className="card main-layout">
            <Card.Title className="card-header-first">{props.tableType} Level Exceptions</Card.Title>
            <div style={{height: '100%', overflowY: 'scroll', overflowX: 'hidden'}}>
                <Table>
                    <thead className="table-header-row">
                    <tr>
                        <td className="table-header-row">Exception Level</td>
                        <td className="table-header-row">Entity Code</td>
                        <td className="table-header-row">Exception Type</td>
                        <td className="table-header-row">Process</td>
                        <td className="table-header-row">Security ID</td>
                        <td className="table-header-row">Calculation Date</td>
                        <td className="table-header-row">Status</td>
                    </tr>
                    </thead>
                    <tbody style={{height: '100%', overflow: 'scroll'}}>
                    {securityExceptions}
                    </tbody>
                </Table>
            </div>
        </Card>
    );
};
export default CalculationSecurityExceptions;